<template>
  <div id="cashier">
  	<commonHeader :title="'收银台'"></commonHeader>
    <div class="cell bg-fff bor-none mb-20">
      订单金额
      <span class="fl-right" style="color: #fe4436;">￥700</span>
    </div>

    <div class="cell bg-fff">选择付款方式</div>
    <div class="cell bg-fff" style="height: 1.87rem;" >
      <div class="weixin">
        <span class="pay-type">微信支付</span>
        <span class="pay-remind">推荐使用</span>
        <img src="../../assets/arrow_gray.png" class="arrow_gray">
      </div>

    </div>
    <div class="cell bg-fff"  style="height: 1.87rem;">
      <div class="wallet">
        <span class="pay-type">我的钱包</span>
        <span class="pay-remind">使用钱包余额支付</span>
        <img src="../../assets/arrow_gray.png" class="arrow_gray">
      </div>

    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {}
    },
    watch: {},
    methods: {
      showRemind(){
        this.$popup({
          title: '',
          content:''
        }).then((res) => {
          console.log(res)
        })
      }

    }
  }
</script>

<style lang="scss">
  @import "../../sass/baseSass.scss";
  #cashier{
    min-height: 100%;
    background: #f0f0f0;

    .bor-none{
      border: none;
    }
    .mb-20{
      margin-bottom: 0.27rem;
    }

    .weixin,
    .wallet
    {
      height: 1.87rem;
      width: 100%;
      line-height: 0.7rem;
      padding-top: 0.21rem;
      padding-left: 0.88rem;

      span{
        display: block;
      }
      .pay-type{
        font-size: 0.4rem;
        color: #333;
      }
      .pay-remind{
        font-size: 0.37rem;
        color: #999;
      }
    }
    .weixin{
      background: url("../../assets/cart/icon_wechat.png") no-repeat center left;
      background-size: 0.59rem auto;
    }
    .wallet{
      background: url("../../assets/cart/ico_wallet.png") no-repeat center left;
      background-size: 0.59rem auto;
    }
  }

</style>



